.registration__container {
    max-width: 1140px;
    margin: 0 auto;
  }
  .registration__title {
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    padding-top: 140px;
    margin-bottom: 94px;
  }
  .registration__list {
    display: flex;
    justify-content: space-between;
  }
  .registration__item {
    border-radius: 31px;
    width: 100%;
    max-width: 263px;
  }
  .registration__item:nth-child(1) {
    background-color: #2f76a6;
    position: relative;
  }
  .registration__item:nth-child(1)::before {
    content: "";
    position: absolute;
    background-image: url("https://s8.hostingkartinok.com/uploads/images/2020/10/b2ab436ccde2251e9adb07d359fa960c.png");
    width: 45px;
    height: 38px;
    top: 18px;
    left: 28px;
  }
  .registration__item:nth-child(2) {
    background-color: #3d67ba;
    position: relative;
  }
  .registration__item:nth-child(2)::before {
    content: "";
    position: absolute;
    background-image: url("https://s8.hostingkartinok.com/uploads/images/2020/10/0930374b1d474ae36d185560d76e7250.png");
    width: 39px;
    height: 38px;
    top: 18px;
    left: 28px;
  }
  .registration__item:nth-child(3) {
    background-color: #ff7b00;
    position: relative;
  }
  .registration__item:nth-child(3)::before {
    content: "";
    position: absolute;
    background-image: url("https://s8.hostingkartinok.com/uploads/images/2020/10/36d8e862f7a851f53ce9d2042fbdb0a5.png");
    width: 19px;
    height: 40px;
    top: 14px;
    left: 32px;
  }
  .registration__item:nth-child(4) {
    background-color: #ff4934;
    position: relative;
  }
  .registration__item:nth-child(4)::before {
    content: "";
    position: absolute;
    background-image: url("https://s8.hostingkartinok.com/uploads/images/2020/10/3458fa2e9b566763172cc49f00e651dd.png");
    width: 31px;
    height: 37px;
    top: 18px;
    left: 28px;
  }
  .registration__link {
    color: #fff;
    font-size: 28px;
    padding: 20px 80px;
  }
  .registration__od {
    font-size: 18px;
    padding: 25px 68px;
  }
  
  .mail__container {
    max-width: 1140px;
    margin: 0 auto;
  }
  .mail__title {
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    margin-top: 150px;
    margin-bottom: 82px;
  }
  .mail__form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .mail__input {
    font-size: 18px;
    width: 100%;
    max-width: 555px;
    padding: 22px 0 25px 20px;
    outline: none;
    border-radius: 31px;
    border: 1px solid #999;
    margin-bottom: 42px;
  }
  .mail__day {
    font-size: 18px;
    width: 100%;
    max-width: 555px;
    padding: 22px 0 25px 20px;
    outline: none;
    border-radius: 31px;
    border: 1px solid #999;
    margin-bottom: 42px;
  }
  .mail__check {
    margin-top: 10px;
  }
  .mail__button {
    font-size: 18px;
    border-radius: 31px;
    text-transform: uppercase;
    color: #fff;
    width: 100%;
    max-width: 585px;
    border: none;
    background: linear-gradient(0deg, #61ba6d 0%, #88cf2d 100%);
    box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.15);
    padding: 23px 0 23px;
    outline: none;
    cursor: pointer;
    margin-top: 62px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
  }
  .mail__button2 {
    font-size: 18px;
    border-radius: 31px;
    text-transform: uppercase;
    color: #fff;
    width: 100%;
    max-width: 585px;
    border: none;
    background: linear-gradient(0deg, #050505 0%, #020202 100%);
    box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.15);
    padding: 23px 0 23px;
    outline: none;
    cursor: pointer;
    margin-top: 62px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
  }
  .mail__info {
    font-size: 18px;
    color: #999;
    display: flex;
    justify-content: center;
    margin-top: 38px;
    margin-left: 124px;
  }